/* About section-5 */

.about-sec5 img{
    position: relative;
    overflow: hidden;
    z-index: -5;
    height: 500px;
    width: 100%;
    filter: blur(5px);
    
    
    
}

.sec5-one{
    margin-top: -450px;
    z-index: 5;
    text-align: center;
    color: white;
    text-decoration: underline gold;
    
   
}




 .sec5-two p{
   
    text-align: center;
    margin-left: 150px;
    color: #ffdb10;
    
    width:300px ;
  font-size: 16px;
  font-weight: 500;
  display: block;
  padding: 14px 15px;
  border-radius: 5px;
  background-color: rgb(54, 43, 43);
  border-left: 3px solid #006dba;
  /* -webkit-box-shadow: -1px 3px 20px 0px rgb(82, 90, 101, 0.1); */
 box-shadow: -1px 3px 20px 0px rgb(82, 90, 101, 0.1);
  margin-bottom: 30px;
}

.sec5-two p:hover{
    width: 300px;
    background-color: #006dba;
    transition: 0.5s;
    color: gold;
    cursor: pointer;
}

@media screen and (max-width:576px) {
  .about-sec5 img{
    height: 800px;
    
  }
  }

  @media screen and (max-width:576px) {
    .sec5-one {
      margin-top: -800px;
      
     
    
    }
  
}

@media screen and (min-width: 576px) and (max-width: 768px){
    .about-sec5 img {
      height: 800px;
      z-index: -10;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .sec5-one {
      margin-top: -800px;
    }
}


